'use strict'
import Mock from 'mockjs'

import {dataPreview} from './mock.dataPreview.js'

function addToMock(list){
  list.forEach(val => {
    Mock.mock(val.path,val.data);
  })
}

addToMock(dataPreview)

export default Mock



