'use strict';
import * as types from './mutations_types.js';

export default {
  [types.SET_NAV_OPEN](state){
    state.width = '190px';
    state.hidden = false;
  },
  [types.SET_NAV_CLOSE](state){
    state.width = '50px';
    state.hidden = true;
  }
}




