import Vue from 'vue'
import Router from 'vue-router'
// import Hello from '@/components/Hello'

Vue.use(Router)

const login = r => require.ensure([], () => r(require('@/components/login/login')), 'login');
const main = r => require.ensure([], () => r(require('@/components/common/main/main')), 'main');
const dataPreview = r => require.ensure([], () => r(require('@/components/data/dataPreview')), 'dataPreview');
const dataMain = r => require.ensure([], () => r(require('@/components/data/dataMain')), 'dataMain');

export default new Router({
  routes: [
    {
      path:'',
      redirect:'/login'
    },
    {
      name:'login',
      path:'/login',
      component:login
    },
    {
      name:'main',
      path:'/main',
      component:main,
      children:[
        {
          name:'dataPreview',
          path:'dataPreview',
          component:dataPreview
        },
        {
          name:'dataMain',
          path:'dataMain/:id',
          component:dataMain
        }
      ]
    }
  ]
})




